<template>
  <div class="content">
		<div class="container-fluid" style="min-height:70vh;">

    <transition name="fade-in">
      <div v-if="assessment.showModal" class="assessment-modal-overlay">
          <div class="assessment-modal">
            <div style="vertical-align: middle; display: flex; padding-bottom: 5px;">
              <span class="fas fa-cog fa-3x fa-spin"></span>
              <p style="padding-left: 15px; margin-bottom: 0; margin-top: 15px; text-transform: uppercase; font-weight: 700;">{{assessment.build_title}}</p>
            </div>
            <div>
              <p>Please, wait for this process to finish</p>
            </div>
            <div v-if="assessment.build_status" class="text-center">
              <p style="font-size:18px; font-weight:700;">Current State: {{assessment.build_status}}</p>
            </div>
            <div class="text-center">
                <a v-if="assessment.build_url" style="color:white ;margin-right:10px;" class="btn btn-primary btn-fill btn-sm" :href="assessment.build_url" target="_blank">Check Logs</a>
                <button style="margin-left:10px;" class="btn btn-sm btn-danger btn-fill" @click="cancelAssessment()">Cancel</button>
            </div>
          </div>
      </div>
      </transition>

			<div class="row">
				<div class="col-12" style="margin-top:40px;">
					<card>
						<template class="card-header">
              <div style="display:inline-flex;">
                <router-link to="/select-option" class="btn btn-default btn-simple">
                  <i class="fa fa-arrow-left"></i> <span style="font-weight: bold; padding-left:5px; font-size:18px;" class="card-title">Back</span>
                </router-link>
              </div>
						</template>

						<template class="card-body">
              <div class="text-center col-12 col-sm-12 col-xl-8 col-lg-10 mx-auto" style="justify-content: center;margin-top:20px;">
                <h2 style="color: #76BAC6; font-weight: bold;">Take credit for the quality achievements of your code</h2>
                <h4>Fill in the form below to trigger the assessment process</h4>

                <nav class="nav nav-tabs nav-justified mt-5 mb-4" id="assessmentTypeTabs">
                    <a class="nav-link active" data-toggle="pill" data-target="#assessmentSoftwareForm" type="button">Source Code</a>
                    <a class="nav-link" data-toggle="pill" data-target="#assessmentServiceForm" type="button">Service</a>
                    <a class="nav-link" data-toggle="pill" data-target="#assessmentFairForm" type="button">FAIR</a>
                </nav>

                <div class="tab-content">

                  <div class="tab-pane fade show active" id="assessmentSoftwareForm">


                    <div class="accordion" id="softwareAccordionAssessment">
                      <div class="card">
                        <div class="card-header">
                            <button class="btn btn-link btn-block text-left" type="button" data-toggle="collapse" data-target="#softwareAssessmentFromRepository" >
                              From Repository
                            </button>
                        </div>

                        <div id="softwareAssessmentFromRepository" class="collapse show" data-parent="#softwareAccordionAssessment">
                          <div class="card-body text-left p-4">

                            <div class="row mb-2">
                              <div class="col-12 col-md-8">
                                <base-input type="text" required label="Repository URL" addon-left-icon="fa fa-archive" style="margin-bottom:0" v-model="software.repository.url" />
                                <p v-show="software.error.repository_URL" style="color:red; font-size:12px;padding-left:0; padding-top:6px;"> <i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ software.error.repository_URL }}</p>
                              </div>
                              <div class="col-12 col-md-4">
                                <base-input type="text" label="Branch" placeholder="master" addon-left-icon="fa fa-code-fork" style="margin-bottom:0" v-model="software.repository.branch" />
                              </div>
                            </div>

                            <div class="row ml-1 mb-3">

                              <base-checkbox-new name="software_repository_credentials" label="This is a private repository" :disabled="!software.repository.url" style="padding-left: 6px;" v-model="software.repository.private" />

                              <div v-if='software.repository.url && software.repository.private' style="margin-top: 10px; margin-bottom: 20px; padding-left:36px; padding-right: 30px;">

                                <div style="margin-top: 16px;">Personal Access Token</div>
                                <div class="custom-label">Personal access tokens are an alternative to using passwords for authentication and are intended to access resources on behalf of yourself.</div>
                                <div class="custom-label" style="margin-bottom: 12px;">Creating a personal access token documentation &#8594; <a href='https://docs.github.com/en/authentication/keeping-your-account-and-data-secure/creating-a-personal-access-token' target="_blank">GitHub</a> - <a href='https://docs.gitlab.com/ee/user/profile/personal_access_tokens.html' target="_blank">GitLab</a> </div>

                                <p v-show="software.error.repository_credentials" style="color:red; font-size:12px;padding-left:0; padding-top:6px;"> <i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ software.error.repository_credentials }}</p>

                                <base-input type="text" required label="GitHub/GitLab Username" addon-left-icon="fa fa-user" style="margin-left: 16px; padding-right: 14px;" v-model="software.repository.credentials.username" />

                                <base-input type="text" required concealable label="Personal Access token" addon-left-icon="fa fa-key" v-model="software.repository.credentials.access_token" style="padding-left: 16px;" />

                              </div>

                            </div>

                            <div class="row mb-2">
                              <div class="col-12 col-md-8">
                                <base-input type="text" class="no-margin" label="External documentation repository URL" addon-left-icon="fa fa-book" v-model="software.documentation.url" />
                                <p v-show="software.error.documentation_URL" style="color:red; font-size:12px;padding-left:0; padding-top:6px;"> <i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ software.error.documentation_URL }}</p>
                              </div>
                              <div class="col-12 col-md-4">
                                <base-input type="text" class="no-margin" label="Branch" placeholder="master" addon-left-icon="fa fa-code-fork" v-model="software.documentation.branch" />
                              </div>
                            </div>

                            <div class="row ml-1 mb-3">

                              <base-checkbox-new name="software_documentation_credentials" label="This is a private repository" :disabled="!software.documentation.url" style="padding-left: 6px;" v-model="software.documentation.private" />

                              <div v-if='software.documentation.url && software.documentation.private' style="margin-top: 16px; margin-bottom: 20px; padding-left:36px; padding-right: 30px;">

                                <div style="margin-top: 16px;">Personal Access Token</div>
                                <div class="custom-label">Personal access tokens are an alternative to using passwords for authentication and are intended to access resources on behalf of yourself.</div>
                                <div class="custom-label" style="margin-bottom: 12px;">Creating a personal access token documentation &#8594; <a href='https://docs.github.com/en/authentication/keeping-your-account-and-data-secure/creating-a-personal-access-token' target="_blank">GitHub</a> - <a href='https://docs.gitlab.com/ee/user/profile/personal_access_tokens.html' target="_blank">GitLab</a> </div>

                                <p v-show="software.error.documentation_credentials" style="color:red; font-size:12px;padding-left:0; padding-top:6px;"> <i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ software.error.documentation_credentials }}</p>

                                <base-input type="text" required label="GitHub/GitLab Username" addon-left-icon="fa fa-user" style="margin-left: 16px; padding-right: 14px;" v-model="software.documentation.credentials.username" />

                                <base-input type="text" required concealable label="Personal Access token" addon-left-icon="fa fa-key" v-model="software.documentation.credentials.access_token" style="padding-left: 16px;" />

                              </div>

                            </div>



                            <div class="row mb-4">
                              <div class="col-12 col-md-6">
                                <label>Choose a criteria for customize</label>
                                  <div v-if="!software.custom_criteria.available.length" class="ssc-head-line" style="margin: 7px 0;" />
                                  <div v-else class="input-group">
                                    <div class="input-group-prepend">
                                      <span class="input-group-text"> <i class="fa fa-compass"></i> </span>
                                    </div>
                                    <select class="custom-select" v-model='software.custom_criteria.selected' >
                                      <option v-show="software.custom_criteria.selected" value="">None</option>
                                      <option style="text-transform:capitalize;" v-for="(criteria,key) in software.custom_criteria.available" :key="key" :value="criteria">{{criteria}}</option>
                                    </select>
                                  </div>
                              </div>
                            </div>

                            <div v-if="software.custom_criteria.selected" class="row">
                              <div class="col-12 col-md-6">
                                <label><span style="color: crimson">*</span> Choose a tool</label>
                                <div class="input-group">
                                  <div class="input-group-prepend">
                                    <span class="input-group-text"> <i class="fa fa-cogs"></i> </span>
                                  </div>
                                  <select class="custom-select" v-model='software.custom_criteria.tool.selected_name' >
                                    <option style="text-transform:capitalize;" v-for="(tool,key) in software.custom_criteria.tool.available" :key="key" :value="tool['name']">{{tool['name'].toUpperCase()}}</option>
                                  </select>
                                </div>
                                <p v-show="software.error.tool_selection" style="color:red; font-size:12px; padding-top:6px; padding-left:0;"> <i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ software.error.tool_selection }}</p>
                              </div>
                            </div>

                            <div v-if="software.custom_criteria.tool.selected_name">

