                                <div class="col-12">
                                  <span v-if="software.custom_criteria.tool.custom_image" class="badge badge-secondary" style="margin-right: 5px;">image:<span style="font-weight:bold;"> {{software.custom_criteria.tool.custom_image}}</span></span>
                                  <span v-else-if="software.custom_criteria.tool.docker_image" class="badge badge-secondary" style="margin-right: 5px;">image:<span style="font-weight:bold;"> {{software.custom_criteria.tool.docker_image}}</span></span>
                                  <span v-if="!software.custom_criteria.tool.custom_image && software.custom_criteria.tool.docker_lang" class="badge badge-primary" style="margin:0 6px;">lang:<span style="font-weight:bold"> {{software.custom_criteria.tool.docker_lang}}</span></span>
                                  <span v-if="!software.custom_criteria.tool.custom_image && software.custom_criteria.tool.docker_version" class="badge badge-danger" style="margin:0 6px;">version:<span style="font-weight:bold"> {{software.custom_criteria.tool.docker_version}}</span></span>
                                </div>
                              </div>

