                                <div class="col-12">

                                <p v-show="software.error.tool_args" style="color:red; font-size:12px; padding-left:0; padding-top:14px;"> <i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ software.error.tool_args }}</p>
