                      </div>


                      <div class="card">
                        <div class="card-header">
                            <button class="btn btn-link btn-block text-left collapsed" type="button" data-toggle="collapse" data-target="#softwareAssessmentFromJSON" >
                              Load QAA report from previous assessment
                            </button>
                        </div>

                        <div id="softwareAssessmentFromJSON" class="collapse"  data-parent="#softwareAccordionAssessment">
                          <div class="card-body">

                            <div class="text-left mb-4">

                            <div class="row mt-2 mb-2"><div class="col">You can review a previously generated SQAaaS report </div></div>
                            <p v-show="software.error.json" style="color:red; font-size:12px;padding-left:0; padding-top:6px;"> <i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ software.error.json }}</p>

                            <div class="row my-2"><div class="col ml-4 custom-label">By uploading the associated JSON file</div></div>

                            <div class="custom-file text-center">
                              <input type="file" accept="application/JSON" id="softwareJSONInputFile" class="custom-file-input" title="" @dragenter="dragEnterJSON($event.target)" @dragleave="dragLeaveJSON($event.target)" @change="selectJSON($event.target,'software')">
                              <label class="custom-file-label" :class="{ 'json-loaded': software.json.file }">{{ software.json.file? software.json.file.name+" ("+Math.round(software.json.file.size/1024)+"kB)" : "Select or drop an assessment report JSON file" }}</label>
                              <p v-show="software.error.json_file" style="color:red; font-size:12px;padding-left:0; padding-top:10px; text-align: left"> <i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ software.error.json_file }}</p>
                            </div>

                            <div class="row my-2"><div class="col ml-4 custom-label">By typing the URL of the JSON file</div></div>

                            <div class="custom-file text-center">
                              <base-input type="text" v-model="software.json.url" @input="inputURLJSON($event.target, 'software')" />
                              <p v-show="software.error.json_url" style="color:red; font-size:12px; margin-top:-8px; text-align: left"> <i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ software.error.json_url }}</p>
                            </div>

                            </div>

                          </div>
                        </div>
                      </div>
                    </div>

                    <button class="btn btn-primary btn-fill mb-3 startAssessmentButton" :disabled="software.accordion_open === 'none'" @click="startAssessment('software')">Start Source Code Assessment</button>


                  </div>

