                              <div class="row ml-1 mb-3">

                                <base-checkbox-new name="service_repository_credentials" label="This is a private repository" :disabled="!service.repository.url" style="padding-left: 6px;" v-model="service.repository.private" />

                                <div v-if='service.repository.private' style="margin-top: 16px; margin-bottom: 20px; padding-left:36px; padding-right: 30px;">

