                                  <div class="custom-label" style="margin-bottom: 12px;">Creating a personal access token documentation &#8594; <a href='https://docs.github.com/en/authentication/keeping-your-account-and-data-secure/creating-a-personal-access-token' target="_blank">GitHub</a> - <a href='https://docs.gitlab.com/ee/user/profile/personal_access_tokens.html' target="_blank">GitLab</a> </div>

                                  <p v-show="service.error.repository_credentials" style="color:red; font-size:12px;padding-left:0; padding-top:6px;"> <i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ service.error.repository_credentials }}</p>

                                  <base-input type="text" required label="GitHub/GitLab Username" addon-left-icon="fa fa-user" style="margin-left: 16px; padding-right: 14px;" v-model="service.repository.credentials.username" />

                                  <base-input type="text" required concealable label="Personal Access token" addon-left-icon="fa fa-key" v-model="service.repository.credentials.access_token" style="padding-left: 16px;" />

                                </div>

                              </div>

                              <div class="row">
                                <div class="col-12 col-md-6">
                                  <label><span style="color: crimson">*</span> Choose a tool </label>
                                  <div v-if="!service.tool.available.length" class="ssc-head-line" style="margin: 7px 0;" />
                                  <div v-else class="input-group">
                                    <div class="input-group-prepend">
                                      <span class="input-group-text"> <i class="fa fa-cogs"></i> </span>
                                    </div>
                                    <select class="custom-select" v-model='service.tool.selected_name' >
                                      <option style="text-transform:capitalize;" v-for="(tool,key) in service.tool.available" :key="key" :value="tool['name']">{{tool['name'].toUpperCase()}}</option>
                                    </select>
                                  </div>
                                  <p v-show="service.error.tool_selection" style="color:red; font-size:12px; padding-top:6px; padding-left:0;"> <i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ service.error.tool_selection }}</p>
                                </div>
                              </div>

                              <div v-if="service.tool.selected_name">

                                <div class="row mt-3">
                                  <div class="col-12">
                                    <span v-if="service.tool.custom_image" class="badge badge-secondary" style="margin-right: 5px;">image:<span style="font-weight:bold;"> {{service.tool.custom_image}}</span></span>
                                    <span v-else-if="service.tool.docker_image" class="badge badge-secondary" style="margin-right: 5px;">image:<span style="font-weight:bold;"> {{service.tool.docker_image}}</span></span>
                                    <span v-if="!service.tool.custom_image && service.tool.docker_lang" class="badge badge-primary" style="margin:0 5px;">lang:<span style="font-weight:bold"> {{service.tool.docker_lang}}</span></span>
                                    <span v-if="!service.tool.custom_image && service.tool.docker_version" class="badge badge-danger" style="margin:0 5px;">version:<span style="font-weight:bold"> {{service.tool.docker_version}}</span></span>
                                  </div>
                                </div>

                                <div class="row mt-3 mx-1">
                                  <div class="col-12">

                                    <p v-show="service.error.tool_args" style="color:red; font-size:12px; padding-left:0; padding-top:14px;"> <i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ service.error.tool_args }}</p>
                                    <div v-for="arg in service.tool.selected.args" :key="arg.id" style="margin-top: 16px;">
                                      <div v-if="arg.selectable">

                                        <div v-if="!arg.repeatable && arg.format === 'string'" class="form-group">
                                          <base-input type="text" :label="arg.description" :required="arg.type === 'positional'" v-model="arg.value" />
                                        </div>

                                        <div v-else-if="arg.repeatable && arg.format === 'string'" class="form-group">
                                          <base-input type="text" :label="arg.description" :required="arg.type === 'positional'" v-model="arg.value" tags />
                                        </div>

