                                          <label :for="arg.id"><span v-if="arg.type === 'positional'" style="color: crimson">*</span> {{arg.description}}</label>
                                          <select class="custom-select" :id="arg.id">
                                            <option selected disabled value=''></option>
                                            <option v-for="opt in arg.value" :value="opt">{{opt}}</option>
                                          </select>
                                        </div>

                                      </div>
                                    </div>

                                    <base-input type="text" label="Custom public image" help="Define your own image service. This has to be public." :placeholder="service.tool.docker_image" v-model="service.tool.custom_image" />

                                  </div>
                                </div>

                              </div>

                          </div>
                        </div>
                      </div>


                      <div class="card">
                        <div class="card-header">
                          <button class="btn btn-link btn-block text-left collapsed" type="button" data-toggle="collapse" data-target="#serviceAssessmentFromJSON" >
                            Load QAA report from previous assessment
                          </button>
                        </div>

