                                <label class="custom-file-label" :class="{ 'json-loaded': service.json.file }">{{ service.json.file? service.json.file.name+" ("+Math.round(service.json.file.size/1024)+"kB)" : "Select or drop an assessment report JSON file" }}</label>
                                <p v-show="service.error.json_file" style="color:red; font-size:12px;padding-left:0; padding-top:10px; text-align: left"> <i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ service.error.json_file }}</p>
