                              <div class="custom-file text-center">
                                <base-input type="text" v-model="service.json.url" @input="inputURLJSON($event.target, 'service')" />
                                <p v-show="service.error.json_url" style="color:red; font-size:12px; margin-top:-8px; text-align: left"> <i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ service.error.json_url }}</p>
                              </div>

                            </div>

                          </div>
                        </div>
                      </div>
                    </div>

                    <button class="btn btn-primary btn-fill mb-3 startAssessmentButton" :disabled="service.accordion_open === 'none'" @click="startAssessment('service')">Start Service Assessment</button>

                  </div>

                  <div class="tab-pane fade" id="assessmentFairForm">

                    <div class="accordion" id="fairAccordionAssessment">
                      <div class="card">
                        <div class="card-header">
                          <button class="btn btn-link btn-block text-left" type="button" data-toggle="collapse" data-target="#fairAssessmentFromRepository" >
                            From Repository
                          </button>
                        </div>

                        <div id="fairAssessmentFromRepository" class="collapse show" data-parent="#fairAccordionAssessment">
                          <div class="card-body text-left p-4">

                                <div class="row">
                                  <div class="col-12 col-md-6">
                                    <label><span style="color: crimson">*</span> Choose a tool</label>
                                    <div v-if="!fair.tool.available.length" class="ssc-head-line" style="margin: 7px 0;" />
