<template>
  <div class="form-check" :class="{disabled}">
    <label class="form-check-label">
      <input type="checkbox" class="form-check-input" :disabled="disabled" v-model="model" />
      <span class="form-check-sign"></span>
      <span v-if="label" class="cbLabel">{{label}}</span>
    </label>
  </div>
</template>
<script>
  export default{

    name: 'base-checkbox-new',

    props: {
      label: String,
      disabled: Boolean,
      checked: Boolean,
    },

    data () {
      return {}
    },

    computed: {
      model: {
        get () {
          return this.checked
        },
        set(check) {
          this.$emit('input', check)
        }
      },
    }
  }
</script>

<style scoped>
  .form-check-sign::before,
  .form-check-sign::after{
    margin-top: 1px!important;
  }
  .cbLabel{
    font-size: 16px;
    padding-left: 5px;
  }
</style>
