import axios from "axios";
import store from "@/store";

export default {
  data(){
    return {}
  },
  methods: {
    login_egi(){
      if (this.$route.query.redirect) sessionStorage.setItem("redirect", this.$route.query.redirect);
      else sessionStorage.removeItem("redirect")
      this.$router.push("/oidc")
    },
    logout_egi(){
      localStorage.removeItem('session')
      store.state.isLogged = false
      this.$router.push('/')
    },

      checkauthCall(callBackHandler){
        var session = JSON.parse(localStorage.getItem("session"));
        var token = session.access_token;
        $.ajax({
          url: process.env.VUE_APP_AUTHORITY + '/protocol/openid-connect/userinfo',
          type: 'POST',
          contentType: 'application/json',
          headers: {
            'Authorization': 'Bearer ' + token
        },
          success: function (result) {
            callBackHandler(result)
          },
          error: function (error) {
            callBackHandler(error)
          }
        });
      },
      createPipelineCall(data, callBackHandler) {
          axios({
              method: 'post',
              url: process.env.VUE_APP_API + '/pipeline',
              data: data
          }).then(response => {
              return callBackHandler(response);
          })
          .catch(error => {
              return callBackHandler(error.response);
          });
      },
      updatePipelineCall(id, data,callBackHandler) {
          axios({
              method: 'put',
              url: process.env.VUE_APP_API + '/pipeline/'+id,
              data:data

          }).then(response => {
              return callBackHandler(response);
          })
          .catch(error => {
              return callBackHandler(error.response);
          });
      },
      deletePipelineCall(pipeline_id, callBackHandler) {
          axios({
              method: 'delete',
              url: process.env.VUE_APP_API + '/pipeline/' + pipeline_id,
          }).then(response => {
              return callBackHandler(response);
          })
          .catch(error => {
              return callBackHandler(error.response);
          });
      },
      runPipelineCall(data, callBackHandler){
        axios({
          method: 'post',
          url: process.env.VUE_APP_API + '/pipeline/' + data.id + '/run?issue_badge=true&repo_url=' + data.url + '&repo_branch=' + data.branch + '&keepgoing=' + data.keepgoing,
        }).then(response => {
            return callBackHandler(response);
        })
        .catch(error => {
            return callBackHandler(error.response);
        });

      },
      checkStatusCall(pipeline_id, callBackHandler){
        axios({
          method: 'get',
          url: process.env.VUE_APP_API + '/pipeline/' + pipeline_id + '/status',
        }).then(response => {
            return callBackHandler(response);
        })
        .catch(error => {
            return callBackHandler(error.response);
        });

      },
    downloadFileCall(id, callBackHandler){
      axios({
          method: 'get',
          url: process.env.VUE_APP_API + '/pipeline/' + id + '/compressed_files',
          contentType:'application/zip',
          responseType: 'blob'
        }).then(response => {
            return callBackHandler(response);
        })
        .catch(error => {
            return callBackHandler(error.response);
        });

    },
    pullRequestCall(id,data, callBackHandler){
      axios({
          method: 'post',
          url: process.env.VUE_APP_API + '/pipeline/' + id + '/pull_request',
          data: data
        }).then(response => {
            return callBackHandler(response);
        })
        .catch(error => {
            return callBackHandler(error.response);
        });


    },
    getConfigCall(id,callBackHandler){
         axios({
          method: 'get',
          url: process.env.VUE_APP_API + '/pipeline/' + id + '/config/jepl',
        }).then(response => {
            return callBackHandler(response);
        })
        .catch(error => {
            return callBackHandler(error.response);
        });

    },
    getBuilderScriptCall(id,callBackHandler){
         axios({
          method: 'get',
          url: process.env.VUE_APP_API + '/pipeline/' + id + '/config/scripts',
        }).then(response => {
            return callBackHandler(response);
        })
        .catch(error => {
            return callBackHandler(error.response);
        });

    },
    getComposerCall(id,callBackHandler){
         axios({
          method: 'get',
          url: process.env.VUE_APP_API + '/pipeline/' + id + '/composer/jepl',
        }).then(response => {
            return callBackHandler(response);
        })
        .catch(error => {
            return callBackHandler(error.response);
        });

    },
    getJenkCall(id,callBackHandler){
         axios({
          method: 'get',
          url: process.env.VUE_APP_API + '/pipeline/' + id + '/jenkinsfile/jepl',
        }).then(response => {
            return callBackHandler(response);
        })
        .catch(error => {
            return callBackHandler(error.response);
        });

    },
    getBadgeCall(id,callBackHandler){
         axios({
          method: 'post',
          url: process.env.VUE_APP_API + '/pipeline/' + id + '/badge',
        }).then(response => {
            return callBackHandler(response);
        })
        .catch(error => {
            return callBackHandler(error.response);
        });

    },
    getBadgeCallGET(id,callBackHandler){
         axios({
          method: 'get',
          url: process.env.VUE_APP_API + '/pipeline/' + id + '/badge?share=html',
        }).then(response => {
            return callBackHandler(response);
        })
        .catch(error => {
            return callBackHandler(error.response);
        });

    },
    getCriteriaCall(callBackHandler, assessment = false){
      let path = assessment? '/criteria?assessment=true' : '/criteria'
      axios.get(process.env.VUE_APP_API + path)
        .then (response => callBackHandler(response))
        .catch(error    => callBackHandler(error.response))
    },

    // Full Assessment Module

    createAssessmentPipelineCall(data, callBackHandler){
      axios({
        method: 'post',
        url: process.env.VUE_APP_API + '/pipeline/assessment',
        data: data
     }).then(response => {
        return callBackHandler(response);
     })
     .catch(error => {
        return callBackHandler(error.response);
     });

    },
    runAssessmentPipelineCall(pipeline_id, callBackHandler){
      axios({
        method: 'post',
        url: process.env.VUE_APP_API + '/pipeline/' + pipeline_id + '/run',
      }).then(response => {
        return callBackHandler(response);
      })
      .catch(error => {
        return callBackHandler(error.response);
      });
    },
    getOutputCall(pipeline_id, callBackHandler){
      axios({
        method: 'get',
        url: process.env.VUE_APP_API + '/pipeline/assessment/' + pipeline_id + '/output',
      }).then(response => {
        return callBackHandler(response);
      })
      .catch(error => {
        return callBackHandler(error.response);
      });
    }

  }
}
